import React from 'react';
import { Link } from "gatsby";

import "./styles.scss";

const HeadlineCard = ({ data }) => {
    const {frontmatter} = data
    return (
        <Link to={`/${frontmatter.slug}`} className={`BigHeadlineCard`}>
            <div className="imgContainer">
                <img src={frontmatter.cover_photo} alt={frontmatter.title} />
            </div>

            <div className="descriptionContainer">
                <div className="title">
                    {frontmatter.title}
                </div>
                <div className="date">
                    {frontmatter.date}
                </div>
            </div>
        </Link>
    );
};

export default HeadlineCard;
